
'use client';

import { useEffect, useState, useTransition, use } from 'react';
import Image from 'next/image';
import { getEventStatus } from '@/lib/event-utils'; 
import { getSessionUser, joinEventAction, getEventDetailsAction } from '@/lib/actions'; 
import type { Event, Participant } from '@/lib/types';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import UserAvatar from '@/components/user-avatar';
import { Badge } from '@/components/ui/badge';
import { CalendarDays, Users, KeyRound, CheckCircle, Loader2, AlertTriangle, QrCode, UserCheck, LogIn, UserPlus, ArrowLeft, SendToBack } from 'lucide-react';
import { format, parseISO } from 'date-fns';
import { zhCN } from 'date-fns/locale';
import { useToast } from '@/hooks/use-toast';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import ReactMarkdown from 'react-markdown';
import { ScrollArea } from '@/components/ui/scroll-area';
import { useRouter } from 'next/navigation';
import LoadingLink from '@/components/loading-link';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { useLoading } from '@/contexts/loading-context';

interface EventPageProps {
  params: { id: string };
}

export default function EventDetailPage({ params: paramsProp }: EventPageProps) {
  const params = use(paramsProp); 
  const { id } = params;
  const { startLoading, stopLoading } = useLoading();
  const router = useRouter();

  const [event, setEvent] = useState<Event | null>(null);
  const [currentUser, setCurrentUser] = useState<Participant | null>(null);
  const [isLoading, setIsLoading] = useState(true); 
  const [isJoiningEvent, startJoinEventTransition] = useTransition();
  const [cdKeyInput, setCdKeyInput] = useState('');
  const { toast } = useToast();
  

  useEffect(() => {
    async function fetchData() {
      if (!id) {
        setIsLoading(false);
        return;
      }
      setIsLoading(true); 
      startLoading(); 
      try {
        const fetchedEvent = await getEventDetailsAction(id); 

        if (fetchedEvent) {
          setEvent(fetchedEvent);
          const sessionUser = await getSessionUser();
          if (sessionUser) {
            const participantInfo = fetchedEvent.participants.find(p => p.id === sessionUser.id);
            setCurrentUser(participantInfo || { ...sessionUser, isCheckedIn: false, joinedEventIds: sessionUser.joinedEventIds || [] });
          } else {
            setCurrentUser(null);
          }
        } else {
          setEvent(null);
        }
      } catch (error) {
        console.error("获取活动数据失败:", error);
        setEvent(null);
        toast({ title: '错误', description: '无法加载活动详情。', variant: 'destructive' });
      } finally {
        setIsLoading(false); 
        stopLoading(); 
      }
    }
    fetchData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [id, toast]); 

  const handleJoinEvent = async () => {
    if (!event || !currentUser || !cdKeyInput) return;
    startLoading();
    startJoinEventTransition(async () => {
      try {
        const result = await joinEventAction(event.id, cdKeyInput);
        if (result.success && result.event) {
          toast({ title: '成功加入！', description: result.message });
          setEvent(result.event);
          const participantInfo = result.event.participants.find(p => p.id === currentUser.id);
          setCurrentUser(participantInfo || null);
          setCdKeyInput('');
        } else {
          toast({ title: '加入失败', description: result.message, variant: 'destructive' });
        }
      } finally {
        stopLoading();
      }
    });
  };
  
  const handleBackNavigation = () => {
    startLoading(); 
    router.back();
  };

  if (isLoading) { 
    return (
      <div className="flex justify-center items-center min-h-[calc(100vh-15rem)]">
        <Loader2 className="h-12 w-12 animate-spin text-primary" />
      </div>
    );
  }

  if (!event) {
    return (
      <div className="text-center">
        <AlertTriangle className="mx-auto h-12 w-12 text-destructive" />
        <AlertTitle className="mt-4 text-2xl font-semibold">活动未找到</AlertTitle>
        <AlertDescription className="mt-2 text-muted-foreground">
          您查找的活动不存在或已被删除。
        </AlertDescription>
        <Button onClick={() => { startLoading(); router.push('/');}} className="mt-6">返回首页</Button>
      </div>
    );
  }

  const eventStatus = getEventStatus(event.date);
  const formattedDate = format(parseISO(event.date), 'yyyy年MM月d日 HH:mm', { locale: zhCN });
  const isParticipant = currentUser && event.participants.some(p => p.id === currentUser.id);
  const canProceedToActualCheckIn = eventStatus === 'current' && isParticipant && !currentUser?.isCheckedIn;
  const alreadyCheckedIn = isParticipant && currentUser?.isCheckedIn === true;
  const canJoin = eventStatus !== 'past' && !isParticipant && currentUser;


  const statusTextMap = {
    upcoming: '即将开始',
    current: '进行中',
    past: '已结束'
  };

  const getImageHint = (eventName: string) => {
    if (eventName.toLowerCase().includes('survival') || eventName.toLowerCase().includes('生存')) return "minecraft survival";
    if (eventName.toLowerCase().includes('build') || eventName.toLowerCase().includes('建筑')) return "minecraft build";
    if (eventName.toLowerCase().includes('parkour') || eventName.toLowerCase().includes('跑酷')) return "minecraft parkour";
    if (eventName.toLowerCase().includes('redstone') || eventName.toLowerCase().includes('红石')) return "minecraft redstone";
    return "minecraft event";
  }
  const placeholderImageUrl = event.imageUrl || `https://placehold.co/800x400.png`;


  return (
    <div className="max-w-4xl mx-auto space-y-8"> 
      <Card className="overflow-hidden shadow-xl">
        <div className="relative w-full h-64 md:h-80 bg-secondary/30">
          <Image
            src={placeholderImageUrl}
            alt={event.name}
            layout="fill"
            objectFit="cover"
            data-ai-hint={getImageHint(event.name)}
            className="transition-transform duration-300 hover:scale-105"
          />
        </div>
        <CardHeader className="pt-6">
          <div className="flex justify-between items-start">
            <CardTitle className="text-3xl md:text-4xl font-bold">{event.name}</CardTitle>
            <Badge variant={eventStatus === 'current' ? 'default' : eventStatus === 'upcoming' ? 'secondary' : 'outline'}
                   className={`px-3 py-1.5 text-sm ${eventStatus === 'current' ? 'bg-green-600 text-white' : eventStatus === 'upcoming' ? 'bg-blue-600 text-white' : 'border-gray-500 text-muted-foreground'}`}>
              {statusTextMap[eventStatus]}
            </Badge>
          </div>
          <CardDescription className="text-lg text-muted-foreground flex items-center pt-2">
            <CalendarDays className="mr-2 h-5 w-5" /> {formattedDate}
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-6">
          <div className="prose prose-sm sm:prose-base dark:prose-invert max-w-none bg-muted/20 p-6 rounded-lg shadow">
             <ReactMarkdown
                components={{
                  h1: ({node, ...props}) => <h1 className="text-2xl font-bold my-4 border-b pb-2" {...props} />,
                  h2: ({node, ...props}) => <h2 className="text-xl font-semibold my-3 border-b pb-1" {...props} />,
                  h3: ({node, ...props}) => <h3 className="text-lg font-semibold my-2" {...props} />,
                  p: ({node, ...props}) => <p className="my-2 leading-relaxed" {...props} />,
                  ul: ({node, ...props}) => <ul className="list-disc list-inside my-2 pl-4 space-y-1" {...props} />,
                  ol: ({node, ...props}) => <ol className="list-decimal list-inside my-2 pl-4 space-y-1" {...props} />,
                  li: ({node, ...props}) => <li className="my-1" {...props} />,
                  strong: ({node, ...props}) => <strong className="font-semibold" {...props} />,
                   a: ({node, ...props}) => <LoadingLink className="text-primary hover:underline" {...props} />,
                  code: ({node, inline, className, children, ...props}) => {
                    const match = /language-(\w+)/.exec(className || '')
                    return !inline && match ? (
                     <pre className="bg-gray-800 text-white p-3 rounded-md my-3 overflow-x-auto text-sm"><code className={`language-${match[1]}`} {...props}>{children}</code></pre>
                    ) : (
                      <code className="bg-muted dark:bg-foreground/20 px-1.5 py-0.5 rounded text-sm font-mono" {...props}>{children}</code>
                    )
                  },
                  blockquote: ({node, ...props}) => <blockquote className="border-l-4 border-primary pl-4 italic my-3 text-muted-foreground" {...props} />
                }}
              >{event.description}</ReactMarkdown>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-4 text-sm">
            <div className="flex items-center space-x-2 p-4 bg-secondary/30 rounded-lg shadow-sm">
              <Users className="h-5 w-5 text-primary" />
              <span>参与者: {event.participants.length} / {event.participantLimit || '无限制'}</span>
            </div>
            <div className="flex items-center space-x-2 p-4 bg-secondary/30 rounded-lg shadow-sm">
              <KeyRound className="h-5 w-5 text-primary" />
              <span>活动代码: <Badge variant="outline" className="text-base px-2 py-0.5">{isParticipant || currentUser?.id === event.organizerId || currentUser?.op === 1 ? event.cdKey : '******'}</Badge></span>
            </div>
          </div>

          {currentUser ? (
            <div className="pt-4 space-y-4">
              {canJoin && (
                <Card className="bg-secondary/20 border-primary/50 shadow-md">
                  <CardHeader>
                    <CardTitle className="flex items-center text-primary"><UserPlus className="mr-2 h-6 w-6"/> 加入活动</CardTitle>
                    <CardDescription>输入活动代码 (CD-Key) 以加入。</CardDescription>
                  </CardHeader>
                  <CardContent className="space-y-3">
                    <div>
                      <Label htmlFor="cdKeyInput" className="sr-only">活动代码</Label>
                      <Input
                        id="cdKeyInput"
                        type="text"
                        placeholder="输入活动代码"
                        value={cdKeyInput}
                        onChange={(e) => setCdKeyInput(e.target.value)}
                        className="mb-3"
                      />
                    </div>
                    <Button onClick={handleJoinEvent} disabled={isJoiningEvent || !cdKeyInput} className="w-full">
                      {isJoiningEvent ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <LogIn className="mr-2 h-4 w-4" />}
                      确认加入
                    </Button>
                  </CardContent>
                </Card>
              )}

              {canProceedToActualCheckIn && (
                 <Card className="bg-accent/10 border-accent shadow-md">
                    <CardHeader>
                        <CardTitle className="flex items-center text-accent-foreground"><QrCode className="mr-2 h-6 w-6"/> 活动签到</CardTitle>
                        <CardDescription>此活动当前正在进行中。点击下方前往签到页面。</CardDescription>
                    </CardHeader>
                    <CardContent>
                        <Button asChild className="w-full bg-accent text-accent-foreground hover:bg-accent/90">
                            <LoadingLink href={`/events/${event.id}/check-in`}>
                                <SendToBack className="mr-2 h-4 w-4" /> 前往签到
                            </LoadingLink>
                        </Button>
                    </CardContent>
                </Card>
              )}
              {alreadyCheckedIn && (
                 <Alert variant="default" className="bg-green-100 dark:bg-green-900/50 border-green-500 dark:border-green-600">
                  <UserCheck className="h-5 w-5 text-green-700 dark:text-green-400" />
                  <AlertTitle className="text-green-800 dark:text-green-300">您已签到!</AlertTitle>
                  <AlertDescription className="text-green-700 dark:text-green-400">
                    您已成功为此活动签到。祝您游戏愉快！
                  </AlertDescription>
                </Alert>
              )}
              {eventStatus !== 'current' && isParticipant && !alreadyCheckedIn && (
                 <Alert variant="default" className="bg-muted/50">
                  <CalendarDays className="h-4 w-4" />
                  <AlertTitle>签到信息</AlertTitle>
                  <AlertDescription>
                    此活动的签到尚未开始或已结束。
                  </AlertDescription>
                </Alert>
              )}
               {!isParticipant && eventStatus === 'current' && !canJoin && ( 
                 <Alert variant="destructive">
                  <AlertTriangle className="h-4 w-4" />
                  <AlertTitle>无法签到或加入</AlertTitle>
                  <AlertDescription>
                    您无法签到或加入此活动。可能已满员或您未登录。
                  </AlertDescription>
                </Alert>
              )}
            </div>
          ) : (
             <Alert className="bg-blue-50 dark:bg-blue-900/30 border-blue-400 dark:border-blue-600">
              <LogIn className="h-5 w-5 text-blue-600 dark:text-blue-400" />
              <AlertTitle className="text-blue-700 dark:text-blue-300">登录以参与</AlertTitle>
              <AlertDescription className="text-blue-600 dark:text-blue-400">
                <Button variant="link" className="p-0 h-auto text-blue-600 dark:text-blue-400" asChild><LoadingLink href={`/login?redirect=/events/${event.id}`}>登录</LoadingLink></Button> 后即可加入活动或查看您的签到状态。
              </AlertDescription>
            </Alert>
          )}


          <div>
            <h3 className="text-xl font-semibold mb-4 mt-6 pt-4 border-t">参与者列表 ({event.participants.length})</h3>
            {event.participants.length > 0 ? (
              <ScrollArea className="h-72 rounded-md border shadow-inner">
                <ul className="divide-y divide-border">
                  {event.participants.map(participant => (
                    <li key={participant.id} className="flex items-center justify-between p-3 hover:bg-muted/30 transition-colors">
                      <div className="flex items-center space-x-3">
                        <UserAvatar user={participant} className="h-10 w-10" />
                        <span className="font-medium">{participant.username}</span>
                      </div>
                      {participant.isCheckedIn && <Badge className="bg-primary text-primary-foreground text-xs">已签到</Badge>}
                    </li>
                  ))}
                </ul>
              </ScrollArea>
            ) : (
              <p className="text-muted-foreground italic py-4 text-center">还没有人加入这个活动。</p>
            )}
          </div>
        </CardContent>
        <CardFooter className="border-t pt-6">
            <Button variant="outline" onClick={handleBackNavigation} className="w-full">
                <ArrowLeft className="mr-2 h-4 w-4" /> 返回
            </Button>
        </CardFooter>
      </Card>
    </div>
  );
}
